<template>
	<div class="window white">
	        <fieldset class='contractsdialog'>
	            <legend>
	            	<button @click='showContracts = 1' class='buttoncontracts'>Contracts</button>
	            	<!--<button @click='showContracts = 3'>DAO Contracts</button> -->
	            </legend>
	            <p class='simple-error'>
	            	Do <b>not</b> send funds directly to the Swerve contracts. You need to deposit them!
	            </p>
	            <div v-show='showContracts == 1'>
		            <fieldset v-for = '(addresses, i) in contractAddresses'>
		            	<legend>swUSD</legend>
		                	<a target="_blank" :href = "'https://etherscan.io/address/' + addresses.swap">
		                		<span class='text'>swap address</span>
		                	</a>
		                	<a target="_blank" :href = "'https://etherscan.io/address/' + addresses.token">
		                		<span class='text'>[ySwerve] ySWRV token address</span>
		                	</a>
		                	<a target="_blank" :href = "'https://etherscan.io/address/' + depositZaps[i].deposit" v-show="!['ren', 'sbtc'].includes(allPools[i])">
		                		 <span class='text'>deposit address</span>
		                	</a>
		                	<a target="_blank" :href= "'https://etherscan.io/address/' + rewardsAddresses[allPools[i]]" v-show="['susdv2', 'sbtc', 'y', 'swusd'].includes(allPools[i])">
		                		 <span class='text'>staking rewards address</span>
		                	</a>
		                	<a target="_blank" href="https://etherscan.io/address/0x9fe350DfA5F66bC086243F21A8F0932514316627" v-show="['ren'].includes(allPools[i])">
		                		 <span class='text'>old adapter address</span>
		                	</a>
		                	<a target="_blank" :href="'https://etherscan.io/address/' + adapterAddresses[allPools[i]]" v-show="['ren','sbtc'].includes(allPools[i])">
		                		 <span class='text'>adapter address</span>
		                	</a>

		            </fieldset>
	        	</div>
	        </fieldset>
	    </div>
</template>

<script>
	import allabis from '../allabis'

	export default {
		data: () => ({
			//allPools: ['compound', 'usdt', 'y', 'busd', 'susdv2', 'pax', 'ren', 'sbtc'],
			allPools: ['y'],
			tokenNames: [
				{ name: 'cCurve', ticker: 'cCrv' },
				{ name: 'tCurve', ticker: 'tCrv' },
				{ name: 'ySwerve', ticker: 'ySwrv' },
				{ name: 'bCurve', ticker: 'bCrv' },
				{ name: 'sCurve', ticker: 'sCrv' },
				{ name: 'pCurve', ticker: 'pCrv' },
				// { name: 'tbtcCurve', ticker: 'tbtcCrv' },
				{ name: 'renCurve', ticker: 'renCrv' },
				{ name: 'sbtcCurve', ticker: 'sbtcCrv' },
			],
			showContracts: 1,
		}),
		computed: {
			contractAddresses() {
				return Object.keys(allabis).filter(pool => ['y'].includes(pool)).map(pool => 
					({swap: allabis[pool].swap_address, token: allabis[pool].token_address})
				)
			},
			depositZaps() {
				return Object.keys(allabis).filter(pool => !['y', 'susd', 'tbtc'].includes(pool)).map(pool => 
					({deposit: allabis[pool].deposit_address, token: allabis[pool].token_address})
				)
			},
			publicPath() {
				return process.env.BASE_URL
			},
			adapterAddresses() {
				return {
					ren: '0x73aB2Bd10aD10F7174a1AD5AFAe3ce3D991C5047',
					sbtc: '0xAEade605D01FE9a8e9C4B3AA0130A90d62167029',
				}
			},
			rewardsAddresses() {
				return {
					susdv2: '0xdcb6a51ea3ca5d3fd898fd6564757c7aaec3ca92',
					sbtc: '0x13C1542A468319688B89E323fe9A3Be3A90EBb27',
				}
			},
		},
		methods: {
			getTokenUrl(i) {
				let publicPath = process.env.BASE_URL
				return publicPath + 'curveIcons/' + this.tokenNames[i].ticker + '.png'
			},
		},
		metaInfo: {
	      title: 'Swerve.fi :: Contracts',
	      meta: [
	        {'property': 'og:title', 'content': 'https://swerve.fi/'},
	        {'property': 'og:url', 'content': 'https://swerve.fi/'},
	        {'property': 'og:type', 'content': 'website'},
	        {'property': 'og:description', 'content': 'Swerve is an exchange liquidity pool on Ethereum designed for extremely efficient stablecoin trading'},
	        {'property': 'og:image', 'content': '/swerve.png'},
	      ]
	    },
	}
</script>

